@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Bebas Neue', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  animation: background-change infinite 20s linear;
  color: #ffffff;  
}

h1, h2, h3, h4, p{
  font-weight: lighter;
}

p{
  font-family: 'Poppins', sans-serif;  
  font-size: 18px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@keyframes background-change{
  0%   {background: #011a25;}  
  50%  {background: #133d50;}
  100% {background:  #011a25;}
}




.App {
  text-align: center; 
  position: relative; 
  width: 100%;
  height: auto;
  margin: 0 auto;
}

.container{
  margin: 0 auto;
  display: flex;
  align-items: center;    
}

#header{
  width: 245px;
  height:100vh;  
}


#about p{
  text-align: justify;
  margin: 10px auto;
}

#about, #projects{
  width: 60%;
  height: auto;  
}

.page-header{
  box-sizing: content-box;
  width: 100%;    
}

.page-header h1, h3{
  margin: 0px;  
}

.page-header h3{
  font-family: 'Poppins', sans-serif; 
}

#personal-links{
  margin: 10px auto;
}

.section{
  margin: 0 auto;
}

.section-title{
  border-top: solid 1px tomato;
  border-bottom: solid 1px tomato;
  padding: 10px;  
  text-align: center;
}

.description b{
  color: tomato;
}




